'use client'
import { TournamentTableElementProps } from "./TournamentTableElement.interface";

import { TeamElementTable } from "./TeamElementTable.type";

export default function TournamentTableElement(props: TournamentTableElementProps) {

    const tableHeadNames = [
        { name: '', className: 'w-80' },
        { name: props.groupName, className: 'w-400' },
        { name: 'И', className: 'w-100' },
        { name: 'В', className: 'w-100' },
        { name: 'Н', className: 'w-100' },
        { name: 'П', className: 'w-100' },
        { name: 'М', className: 'w-200' },
        { name: 'О', className: 'w-80' }
    ];


    const TableHeadElement = ({ className = '', title = '' }: { className?: string | null | undefined, title?: string | null | undefined }) => {
        return (
                <th className={`px-6 py-3 font-medium text-gray-700 w-[80px] ${className}`}>{title}</th>
        )
    }

    return (
        <table className="w-full">
            <thead className="">
                <tr>
                    {tableHeadNames.map((th) => (
                        <TableHeadElement title={th.name} className={` ${th.className}`} />
                    ))}
                </tr>
            </thead>
        </table>
    )
}